import Vue from 'vue'
import Vuex from 'vuex'
import userAPI from '../api/users'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentUser: {
      id: -1,
      name: "",
      email: "",
      image: "",
      isAdmin: false,
    },
    isAuthenticated: false,
    token: "",
    onlineUsers: []
  },
  getters: {
  },
  // 使用commit 發動 mutations
  mutations: {
    setCurrentUser(state, currentUser) {
      state.currentUser = {
        ...state.currentUser,
        ...currentUser
      }

      state.isAuthenticated = true
      state.token = localStorage.getItem('token')
    },
    revokeAuthentication(state) {
      state.currentUser = {}
      state.isAuthenticated = false
      state.token = ""
      localStorage.removeItem('token')
    },
    addOnlineUsers(state, data) {
      const { newUser, onlineUsers } = data
      if (!state.onlineUsers.find(oldUser => oldUser.id === newUser.id)) {
        state.onlineUsers = onlineUsers
      }
    },
    removeOnlineUsers(state, user) {
      state.onlineUsers = state.onlineUsers.filter(oldUser => oldUser.id !== user.id)
    }
  },
  // 使用dispatch 發動 actions
  actions: {
    async fetchCurrentUser() {
      try {
        const { data } = await userAPI.getCurrentUser()
        this.commit('setCurrentUser', data)
        return true
      } catch (error) {
        console.error(error.message)
        this.commit('revokeAuthentication')
        return false
      }
    }
  },
  modules: {
  }
})
